import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Btn } from 'components/common/button';
// import { ColEditDelete } from 'components/common/index';
import { Card, Row, Col } from 'antd';
import { TableData } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import FormIntegrateTraining from './FormIntegrateTrainning';
import { getStudents, downloadFile } from '../../services/IntegrateTraning';
// import { dateToString } from '../../utils/formatUtil';

class ListIntegratedTraining extends Component {
  state = {
    reload: 0,
    dataTable: [],
    paramSearch: {},
  };

  // componentDidMount = () => {
  //   this.handleChangeQueryParams();
  // };

  // componentWillReceiveProps = () => {
  //   this.handleChangeQueryParams();
  // };

  // handleChangeQueryParams = () => {
  //   this.setState({ paramSearch: getQueryParams() });
  // };

  exportData = () => {
    const { paramSearch } = this.state;
    downloadFile(paramSearch);
  };

  dataTable = data => {
    this.setState({
      dataTable: data,
    });
  };

  dataForm = value => {
    this.setState({ paramSearch: getQueryParams() });
    this.setState({
      paramSearch: value,
    });
  };

  renderActionColumn = () => {};

  renderColumn = () => {
    const { dataTable } = this.state;
    const dataColumnRender =
      dataTable.length > 0 &&
      dataTable[0].criteriaTypes.map(item => ({
        title: `${item.key}`,
        render: (text, record, index) => {
          const dataColumn = dataTable[index].criteriaTypes.filter(value => value.key === item.key);
          return <div>{dataColumn[0].value}</div>;
        },
      }));
    let dataColumn = [
      {
        title: 'Mã SV',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ Tên',
        dataIndex: 'fullName',
        key: 'fullName',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },

      {
        title: 'Tổng điểm',
        dataIndex: 'trainingScoreTotal',
        key: 'trainingScoreTotal',
      },
      {
        title: 'Kỷ Luật',
        dataIndex: 'disciplineLabel',
        key: 'disciplineLabel',
      },
      {
        title: 'Xếp loại rèn luyện',
        dataIndex: 'classificationLabel',
        key: 'classificationLabel',
      },
    ];
    if (dataTable.length > 0) {
      dataColumn = [
        {
          title: 'Mã SV',
          dataIndex: 'studentCode',
          key: 'studentCode',
        },
        {
          title: 'Họ Tên',
          dataIndex: 'fullName',
          key: 'fullName',
        },
        {
          title: 'Ngày sinh',
          dataIndex: 'dob',
          key: 'dob',
        },

        {
          title: 'Tổng điểm',
          dataIndex: 'trainingScoreTotal',
          key: 'trainingScoreTotal',
        },
        ...dataColumnRender,
        {
          title: 'Kỷ Luật',
          dataIndex: 'disciplineLabel',
          key: 'disciplineLabel',
        },
        {
          title: 'Xếp loại rèn luyện',
          dataIndex: 'classificationLabel',
          key: 'classificationLabel',
        },
      ];
    }
    return dataColumn;
  };

  render() {
    const { dataTable } = this.state;
    const dataColumnTable = this.renderColumn();
    return (
      <Card title="">
        <FormIntegrateTraining dataForm={this.dataForm} />
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              formSearch
              allowGetData
              service={getStudents}
              dataTable={this.dataTable || []}
              columns={dataColumnTable}
              rowKey="studentId"
            />
          </Col>
          <Col span={24} style={{ textAlign: 'center' }}>
            {dataTable.length > 0 && (
              <Btn onClick={this.exportData} icon="download">
                Download
              </Btn>
            )}
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListIntegratedTraining {...props} />
  </Page>
));



